import React, { Component } from 'react';
import { Route, Switch, HashRouter } from 'react-router-dom';
import { createStore } from 'redux';
import './App.scss';
import Header from './components/Header';
import Home from './containers/Home';
import MenuLeft from './components/MenuLeft';

import Footer from './components/Footer';

// route開始

import SearchAll from './containers/SearchAll';

// route結束

import ErrorLink from './containers/ErrorLink';

class App extends Component {
  constructor(props) {
    super(props);
    this.state = {
      apiUrl: "apiUrl",
    }
  }

  componentDidMount() {
    let store = createStore(this.counter.bind(this));
    store.subscribe(() =>
      // console.log(store.getState())
      (store.getState())
    )
    store.dispatch({ type: 'INCREMENT' })
    // 1
    store.dispatch({ type: 'INCREMENT' })
    // 2
    store.dispatch({ type: 'DECREMENT' })
    // 1

  }

  render() {
    //true=尚未登入 false=登入

    return (
      <HashRouter>
        <div className="sectionApp">
          <Header apiUrl={this.state.apiUrl}></Header>
          <section className="container-fluid">
            <div className="row">
              <MenuLeft></MenuLeft>
              <div className="col-10 section-right">

                {/* Route部分開始 */}
                <Switch apiUrl={this.state.apiUrl}>

                  {/* 第一階段 */}
                  <Route exact path="/" component={() => <Home apiUrl={this.state.apiUrl} />} />
                  <Route exact path="/SearchAll/:searchPhone" render={(props) => <SearchAll {...props} apiUrl={this.state.apiUrl} />} />
                  <Route exact path="/SearchAll/" render={(props) => <SearchAll {...props} apiUrl={this.state.apiUrl} />} />
                  <Route component={ErrorLink} />
                </Switch>
                {/* Route部分結束 */}

              </div>
            </div>
          </section>
          <Footer></Footer>
        </div>
      </HashRouter>
    )
  }



  counter(state = 0, action) {
    switch (action.type) {
      case 'INCREMENT':
        return state + 1
      case 'DECREMENT':
        return state - 1
      default:
        return state
    }
  }

}
export default App;
