import React, { Component } from 'react';
import './Header.scss';

function LoginBtn(props) {
   if (props.userName === '') {
      return (
         <div className="userCircle">
            <p className="h6">未登入</p>
         </div>
      );
   }

   return (
      <div className="userCircle">
         <p className="h6">您好！{props.userName}</p>
      </div>
   );
}

class Header extends Component {
   constructor(props) {
      super(props);
      this.state = {
         //使用者資訊
         userValue: {
            Name: localStorage.getItem("userName"),
            Token: localStorage.getItem("userToken")
         },
         //通知
         userMsg: {
            MsgOrder: 0
         },
         //url type
         url: window.location.pathname,
         searchPhone:"",
      }
      this.changeUrl = this.changeUrl.bind(this);
      this.logOut = this.logOut.bind(this);
      this.loginTypeApi = this.loginTypeApi.bind(this);
      this.belleOrderApi = this.belleOrderApi.bind(this);
      this.searchAll = this.searchAll.bind(this);
      this.handleChangeText = this.handleChangeText.bind(this);
   }

   componentDidMount() {
      this.loginTypeApi();
      this.belleOrderApi();
   }

   changeUrl(changeUrl) {
      this.setState({
         url: changeUrl
      })
   }

   loginTypeApi() {
      const apiUrl = this.props.apiUrl;
      // const urlApi = apiUrl + '/api/leads/dashboard';

      console.log(apiUrl);
   }

   belleOrderApi() {
      const apiUrl = this.props.apiUrl;
      // const urlApi = apiUrl + '/api/todoes/incomplete';
      console.log(apiUrl);
      this.setState({
         userMsg: {
            MsgOrder: 99
         }
      });
   }

   logOut() {
      localStorage.removeItem("userToken");
      localStorage.removeItem("userName");
      window.location.href = "/";
   }

   searchAll(){
      var regMobile = new RegExp("[09]{2}[0-9]{8}");
      if (!(regMobile.test(this.state.searchPhone))) {
         alert("手機格式錯誤");
         return false;
      }
      else{
         window.location.href="/#/SearchAll/"+this.state.searchPhone
      }
   }

   handleChangeText(event){
      const state = this.state;
      state[event.target.name] = event.target.value;
      // console.log(state);
      this.setState(state);
   }

   render() {

      return (
         <header className="container-fluid mt-3 mb-3">
            <div className="row d-flex justify-content-between align-items-center">
               <div className="nav-left col-2">
                  <img className="w-100" src="https://i7.pngguru.com/preview/52/250/470/killer-whale-drawing-logo-clip-art-killer-whale.jpg"
                     alt="urbank-lead" />
               </div>
               {(this.state.userValue["Token"] === null) ? "" :
                  <div className="nav-right d-flex col-10 justify-content-end align-items-center">
                     <div className="position-relative h-100">
                        <input type="text" id="searchPhone" name="searchPhone" value={this.state.searchPhone} maxLength="10" className="p-1 pl-2 searchHeader" placeholder="輸入客戶手機號碼，快速搜尋" onChange={this.handleChangeText} />
                        <i className="fas fa-search position-absolute top-50 right-10 bg-dark text-white p-1 urbank-cursor-pointer" onClick={this.searchAll.bind(this)}></i>
                     </div>
                     <div className="msg">
                        <i className="fas fa-bell h3"></i>
                        <p className="">{this.state.userMsg["MsgOrder"]}</p>
                     </div>

                     <LoginBtn userName={this.state.userValue["Name"]}></LoginBtn>
                     {(this.state.userValue["Name"] === null) ? "" : <div className="logOut" onClick={this.logOut.bind(this)}><i className="fas fa-power-off"></i>登出</div>}

                  </div>
               }

            </div>
         </header>
      )
   }

}
export default Header;
