import React, { Component } from "react";
import "./MenuLeft.scss";
import { Link } from "react-router-dom";

class MenuLeft extends Component {
  componentDidMount() {
    console.log(this.state.url);
    this.changeUrl(this.state.url);
  }

  constructor(props) {
    super(props);
    this.state = {
      url: window.location.hash,
    };
  }

  changeUrl = (changeUrl) => {
    this.setState({
      url: changeUrl,
    });
  };

  render() {
    return (
      //首頁
      <div className="col-2 section-left menu-left">
        <div
          className={"collapseAll " + (this.state.url === "#/" ? "active" : "")}
        >
          <Link
            to={{
              pathname: "/",
            }}
            onClick={this.changeUrl.bind(this, "#/")}
            className="whiteType"
          >
            <div className="collapseName menuLeftTitle">首頁</div>
          </Link>
        </div>
        <div
          className={
            "collapseAll " +
            (this.state.url === ("#/SearchAll/" || "#/SearchAll")
              ? "active"
              : "")
          }
        >
          <Link
            to={{
              pathname: "/SearchAll/",
            }}
            onClick={this.changeUrl.bind(this, "#/SearchAll")}
            className="whiteType"
          >
            <div className="collapseName menuLeftTitle">搜尋</div>
          </Link>
        </div>
      </div>
    );
  }
}
export default MenuLeft;
