//放大鏡搜尋列
import React, { Component } from "react";
import "./SearchAll.scss";
import DatePicker from "react-datepicker";
import "react-datepicker/dist/react-datepicker.css";

class SearchAll extends Component {
  constructor(props) {
    super(props);
    this.state = {
      CreatedStartAt: "",
      CreatedEndAt: "",
      Name: "",
      Mobile: "",
      apiUrl: this.props.apiUrl,
      searchPhone: this.props.match.params.searchPhone,
      DataAll: [],
    };

    this.searchBarList = this.searchBarList.bind(this);
  }

  handleChangeStartDate = (date) => {
    this.setState({
      CreatedStartAt: date,
    });
  };

  handleChangeEndDate = (date) => {
    this.setState({
      CreatedEndAt: date,
    });
  };

  handleChangeText = (event) => {
    const state = this.state;
    state[event.target.name] = event.target.value;
    this.setState(state);
  };

  componentDidMount() {
   console.log(this.props);
    this.searchBarList();
  }

  componentDidUpdate(prevProps) {
    // 重新搜尋後刷新頁面
    if (this.props !== prevProps) {
      console.log(this.props);
      console.log(prevProps);
      // console.log(this.props.match.params.searchPhone)
      // this.setState({
      //    Mobile:this.props.match.params.searchPhone
      // })
      this.searchBarList();
    }
  }

  searchList = () => {
    //我是查詢
    const getCreatedStartAt = this.state.CreatedStartAt;
    const getCreatedEndAt = this.state.CreatedEndAt;
    const getName = this.state.Name;
    const getMobile = this.state.Mobile;

    if (getMobile === "") {
      alert("電話不得為空");
      return false;
    }

    const apiUrl = this.state.apiUrl;
    const urlApi =
      apiUrl +
      "/api/leads/?CreatedStartAt=" +
      getCreatedStartAt +
      "&CreatedEndAt=" +
      getCreatedEndAt +
      "&Mobile=" +
      getMobile +
      "&Name=" +
      getName;
    console.log(urlApi);

    this.setState({
      DataAll: [
        {
          Name: this.state.Name,
          Mobile: this.state.Mobile,
        },
        {
          Name: this.state.Name,
          Mobile: this.state.Mobile,
        },
        {
          Name: this.state.Name,
          Mobile: this.state.Mobile,
        },
      ],
    });
  };

  searchBarList = () => {
    //搜尋bar的搜尋
    const searchPhone = this.props.match.params.searchPhone;
    const apiUrl = this.state.apiUrl;
    const urlApi = apiUrl + "/api/leads?Mobile=" + searchPhone;
    console.log(urlApi);

    this.setState({
      DataAll: [
        {
          Name: "上面搜尋",
          Mobile: searchPhone,
        },
        {
          Name: "上面搜尋",
          Mobile: searchPhone,
        },
        {
          Name: "上面搜尋",
          Mobile: searchPhone,
        },
      ],
    });
  };

  openRoles = (openId) => {
    //GET /api/leads/searching/{id}/detail
    const apiUrl = this.state.apiUrl;
    const urlApi = apiUrl + "/api/leads/searching/" + openId + "/opentype";
    console.log(urlApi);
  };

  cancel = () => {
    this.setState({
      CreatedStartAt: "",
      CreatedEndAt: "",
      Name: "",
      Mobile: "",
      DataAll: [],
    });
  };

  render() {
    return (
      // 客服電話名單
      <div className="SearchAll">
        <div className="SearchAll__title col-12">
          <i className="fas fa-headset"></i>
          <p>查詢條件</p>
        </div>
        <div className="SearchAll__date col-12">
          <i className="far fa-calendar-alt"></i>
          <div className="input-group date" id="datetimepicker1">
            <DatePicker
              autoComplete="off"
              selected={this.state.CreatedStartAt}
              onChange={this.handleChangeStartDate}
              dateFormat="yyyy/MM/dd"
              id="CreatedStartAt"
              name="CreatedStartAt"
            />
          </div>
          <i className="far fa-calendar-alt"></i>
          <div className="input-group" id="datetimepicker2">
            <DatePicker
              autoComplete="off"
              selected={this.state.CreatedEndAt}
              onChange={this.handleChangeEndDate}
              dateFormat="yyyy/MM/dd"
              id="CreatedEndAt"
              name="CreatedEndAt"
            />
          </div>
          <input
            type="text"
            className="form-control"
            placeholder="請輸入姓名"
            id="Name"
            name="Name"
            value={this.state.Name}
            onChange={this.handleChangeText}
          />
          <input
            type="text"
            className="form-control"
            placeholder="請輸入手機號碼"
            maxLength="10"
            id="Mobile"
            name="Mobile"
            value={this.state.Mobile}
            onChange={this.handleChangeText}
          />
        </div>
        <div className="SearchAll__searchBtn col-12">
          <button className="btn btn-gray" onClick={this.cancel}>
            取消
          </button>
          <button className="btn btn-coffee" onClick={this.searchList}>
            套用
          </button>
        </div>
        <div className="SearchAll__text col-12">
          {this.state.DataAll.length === 0 ? (
            <div>
              <p>無資料</p>
            </div>
          ) : (
            this.state.DataAll.map((item, i) => {
              return (
                <div className="d-flex" key={i}>
                  <p>姓名：{item.Name}。</p>
                  <p>電話：{item.Mobile}。</p>
                </div>
              );
            })
          )}
        </div>
        <div className="SearchAll__table col-12"></div>
      </div>
    );
  }
}
export default SearchAll;
